<script setup>
import { ref, onMounted } from 'vue';
import LocalCom from '../../service/servicelocation/NotrentAppFamillService';
import { useToast } from 'primevue/usetoast';
import { FadeInOut } from 'vue3-transitions'
import axios from 'axios';
import { useRoute } from 'vue-router';
import Loading from 'vue-loading-overlay';
let isLoading =ref(false);
const localcommservice = new LocalCom();
const route = useRoute();  
const toast = useToast();
const Residenceid = route.params.id;
const dt = ref(null);
const LocalcData = ref(null);
const Locataireadd = ref({});
const LocalcView=ref({})
const selectedLocataireaValue = ref(null);
const PaimentLocataire = ref({});
const CompteurLocataire = ref({});
const locationdata=ref({});
const paimentdata=ref({});
const WithContracttoggleValue = ref(false);
const uploadedFiles = ref(false);
const ExistantLocataire = ref([]);
const nestedRouteItems = ref([
    {
        label: 'Locataire',
        to: ''
    },
    {
        label: 'Paiment',
        to: ''
    },
    {
        label: 'Compteur',
        to: ''
    },
    {
        label: 'Document',
        to: ''
    },

]);
const nestedRouteItemsreserv = ref([
    {
        label: 'Locataire',
        to: ''
    },
    {
        label: 'Paiment',
        to: ''
    },
    {
        label: 'Confirmation',
        to: ''
    },

]);
const LocalcaddDialog = ref(false);
const visibleSidebarInfo = ref(false);

const autoFilteredValue = ref([]);


let Appartmenttitledialog = ref('');
let newLocatairedialog = ref(false);

let updateprogressindex=ref(1)
let stepone = ref(false);
let steptwo = ref(false);
let stepthree = ref(false);
let stepfour = ref(false);
let reservationdialog=ref(false)
let selectedFiles = null

const readLocalc = async () =>{
    const response = await localcommservice.getLocalC(Residenceid);
    LocalcData.value=response.data.msg;
    ExistantLocataire.value=response.data.existantlocataire;
    
}
const hidedialog = () => {
    LocalcaddDialog.value=false;
    reservationdialog.value=false;
    isLoading.value=false;
}
const searchLocataire = (event) => {
    setTimeout(() => {
        if (!event.query.trim().length) {
            autoFilteredValue.value = [...ExistantLocataire.value];
        } else {
            autoFilteredValue.value = ExistantLocataire.value.filter((country) => {
                return country.name.toLowerCase().startsWith(event.query.toLowerCase());
            });
        }
    }, 50);
};

const openNew = (appartmentdata) =>{
    selectedLocataireaValue.value=null
    Locataireadd.value = {};
    CompteurLocataire.value={};
    PaimentLocataire.value.startdate=null;
    PaimentLocataire.value.enddate=null;
    CompteurLocataire.value.steg=null;
    CompteurLocataire.value.sonede=null;
    CompteurLocataire.value.gaz=null;
    uploadedFiles.value=false;
    locationdata.value.filesnames=null
    LocalcaddDialog.value=true;
    newLocatairedialog.value=false;

    updateprogressindex.value=1
    stepone.value = true;
    steptwo.value = false;
    stepthree.value = false;
    stepfour.value  = false;

    locationdata.value=appartmentdata;
    PaimentLocataire.value.pcot=appartmentdata.Cotion;
    PaimentLocataire.value.ploc=appartmentdata.Prix;
    setTimeout(() => {
       
        let liststeps = document.getElementsByClassName("p-steps-item");
        liststeps[0].classList.add("p-highlight");
        Appartmenttitledialog.value="Ajouter un nouveau Locataire";

    }, 50);
} 
const ViewLcDetails =(data)=>{
    visibleSidebarInfo.value=true;
    LocalcView.value=data;
}
const newLocataire = () =>{
    Locataireadd.value = {};
    CompteurLocataire.value={};
    
    newLocatairedialog.value=true;
}

let nextstep = () => {
  
    let liststeps = document.getElementsByClassName("p-steps-item");
    if (updateprogressindex.value>=4){
        updateprogressindex.value=4
        liststeps[2].classList.add("p-highlight");
        liststeps[1].classList.remove("p-highlight");
        liststeps[0].classList.remove("p-highlight");
    }
    else{
        for (let i = 0; i < liststeps.length; i++) {
            if (i == updateprogressindex.value) {
                liststeps[i].classList.add("p-highlight");
            } else {
                liststeps[i].classList.remove("p-highlight");
            } 
        }
    }

    updateprogressindex.value++

    switch (updateprogressindex.value) {
        case 1:
            setStep("Ajouter un nouveau Locataire", true, false, false);
        break;
        case 2:
            setStep("Ajouter Le paiment", false, true, false,false);
        break;
        case 3:
            setStep("Ajouter les Compteurs", false, false, true,false);
        break;
        case 4:
            setStep("Ajouter les Documents", false, false, false,true);
        break;
    }
};
let previousstep = () => {
    updateprogressindex.value--;

  let liststeps = document.getElementsByClassName("p-steps-item");
  if (updateprogressindex.value <= 1) {
    updateprogressindex.value = 1;
    liststeps[0].classList.add("p-highlight");
    liststeps[1].classList.remove("p-highlight");
    liststeps[2].classList.remove("p-highlight");
  } else {
    for (let i = 0; i < liststeps.length; i++) {
      if (i + 1 === updateprogressindex.value) {
        liststeps[i].classList.add("p-highlight");
      } else {
        liststeps[i].classList.remove("p-highlight");
      }
    }
  }


    switch (updateprogressindex.value) {
        case 1:
            setStep("Ajouter un nouveau Locataire", true, false, false,false);
        break;
        case 2:
            setStep("Ajouter Le paiment", false, true, false,false);
        break;
        case 3:
            setStep("Ajouter les Compteurs", false, false, true,false);
        break;
        case 4:
            setStep("Ajouter les Documents", false, false, false,true);
        break;
    }
};
let setStep = (title, stepOne, stepTwo, stepThree,stepFour) => {

  Appartmenttitledialog.value = title;
  stepone.value = stepOne;
  steptwo.value = stepTwo;
  stepthree.value = stepThree;
  stepfour.value = stepFour;
};
const changeselectbox = () => {
    if (selectedLocataireaValue.value) {
        Locataireadd.value = selectedLocataireaValue.value;
    }
};

const rentBien= async () =>{
    isLoading.value=true;
    let body={
        LocId:Locataireadd.value.id || "",
        LocFullName:Locataireadd.value.name,
        LocCin:Locataireadd.value.cin,
        LocTelone:Locataireadd.value.telone,
        LocTeltwo:Locataireadd.value.teltwo,

        NumSteg:CompteurLocataire.value.steg,
        NumSonede:CompteurLocataire.value.sonede,
        NumGaz:CompteurLocataire.value.gaz,

        From:PaimentLocataire.value.startdate,
        To:PaimentLocataire.value.enddate,
        PrixLocation:PaimentLocataire.value.ploc,
        Cotion:PaimentLocataire.value.pcot,
        Filesname:locationdata.value.filesnames,
        WithContract:WithContracttoggleValue.value,
    }
    let params = {
        idres:locationdata.value.residentid,
        idbloc:locationdata.value.blocid,
        idetage:locationdata.value.etageid,
        appartementid:locationdata.value.appartementid,
    }
    await localcommservice.rentLocC(params,body).then((response) => {
        readLocalc();
        hidedialog()
        setTimeout(() => {
            LocalcaddDialog.value=false;
            isLoading.value=false;
            toast.add({ severity: response.type, summary: response.summary, detail: response.msg, life: 3000 })

        }, 2000);
        

        
    });
    
}

const onUpload =async (e) =>{
    selectedFiles = e.files;
        if (!selectedFiles || selectedFiles.length === 0) {
            return;
        }

      const formData = new FormData();
      for (let i = 0; i < selectedFiles.length; i++) {
        formData.append("files", selectedFiles[i]);
      }
      formData.append("counter", selectedFiles.length);
      formData.append("appartementid", locationdata.value.appartementid);
      await axios.post(`${process.env.VUE_APP_HOST_BACKEND}/upload/cin`, formData).then((response) => {
            toast.add({ severity: "success", summary: "Action Terminée", detail: "Fichier Ajoutée", life: 5000 });

            uploadedFiles.value = true;
            locationdata.value.filesnames=response.data.files;
        }).catch((error) => { 
            console.log(error);
            uploadedFiles.value = false;
        });
      
}

const openNewreserveation =(data)=>{
    selectedLocataireaValue.value=null
    Locataireadd.value = {};
    CompteurLocataire.value={};
    PaimentLocataire.value.startdate=null;

    newLocatairedialog.value=false;

    reservationdialog.value=true
    updateprogressindex.value=1
    stepone.value = true;
    steptwo.value = false;
    stepthree.value = false;
    stepfour.value  = false;

    locationdata.value=data;
    PaimentLocataire.value.pcot=data.Cotion;
    PaimentLocataire.value.ploc=data.Prix;

    setTimeout(() => {
       
        let liststeps = document.getElementsByClassName("p-steps-item");
        liststeps[0].classList.add("p-highlight");
        Appartmenttitledialog.value="Ajouter un nouveau Locataire";

    }, 50);
}
const submitreserv =async ( ) =>{
    let body={
        LocId:Locataireadd.value.id || "",
        LocFullName:Locataireadd.value.name,
        LocCin:Locataireadd.value.cin,
        LocTelone:Locataireadd.value.telone,
        LocTeltwo:Locataireadd.value.teltwo,

        From:PaimentLocataire.value.startdate,
        PrixLocation:PaimentLocataire.value.ploc,
        Cotion:PaimentLocataire.value.pcot,

    }
    let params = {
        idres:locationdata.value.residentid,
        idbloc:locationdata.value.blocid,
        idetage:locationdata.value.etageid,
        appartementid:locationdata.value.appartementid,
    }

    await localcommservice.reservLoc(params,body).then((response) => {
        hidedialog();
        readLocalc();
        setTimeout(() => {
            LocalcaddDialog.value=false;
            isLoading.value=false;
            toast.add({ severity: response.type, summary: response.summary, detail: response.msg, life: 3000 })
        }, 100);
        
    });
}

onMounted(() => {
    readLocalc();
});
</script>

<template>
    <div class="grid">
        <div class="col-12">
            <div class="card">
                <loading v-model:active="isLoading"/>
                <Toast />
                <Toolbar class="mb-4">
                    <template v-slot:end>
                        <Button label="Exporter" icon="pi pi-upload" class="p-button-help" />
                    </template>
                </Toolbar>
                <DataTable
                    ref="dt"
                    :value="LocalcData"
                    dataKey="name"
                    :paginator="true"
                    :rows="10"
                    paginatorTemplate="FirstPageLink PrevPageLink PageLinks NextPageLink LastPageLink CurrentPageReport RowsPerPageDropdown"
                    :rowsPerPageOptions="[5, 10, 25]"
                    currentPageReportTemplate="Showing {first} to {last} of {totalRecords} Appartments"
                    responsiveLayout="scroll"
                    sortField="residentname" :sortOrder="-1" 

                >   
                <template #empty> Pas D'appartement  . </template>
 
                    <Column field="residentname" header="Residence"  headerStyle="width:5%; min-width:8rem;"  :sortable="true">
                        <template #body="slotProps">
                            {{ slotProps.data.residentname }}
                        </template>
                    </Column>

                    <Column field="etagename" header="Etage Num" headerStyle="width:10%; min-width:10rem;" :sortable="true">
                        <template #body="slotProps">
                            {{  slotProps.data.etagename ==0 ? "RDC" : slotProps.data.etagename}}
                        </template>
                    </Column>
                    <Column field="appartementname" header="Num Apppartement" headerStyle="width:10%; min-width:10rem;" :sortable="true">
                        <template #body="slotProps">
                            {{ slotProps.data.appartementname }}
                        </template>
                    </Column>
                    <Column field="Prix" header="Prix Location" headerStyle="width:10%; min-width:10rem;" :sortable="true">
                        <template #body="slotProps">
                            {{  slotProps.data.Prix }}
                        </template>
                    </Column>
                    <Column field="Cotion" header="Cautionnement" headerStyle="width:10%; min-width:10rem;" :sortable="true">
                        <template #body="slotProps">
                            {{ slotProps.data.Cotion }}
                        </template>
                    </Column>
                    <Column headerStyle="width:20%; min-width:15rem;">
                        <template #body="slotProps">
                            <Button icon="fas fa-eye"  class="p-button-rounded p-button-secondary mr-2 " @click="ViewLcDetails(slotProps.data)"/>
                            <Button icon="fas fa-check"  class="p-button-rounded p-button-success mr-2 " @click="openNew(slotProps.data)" />
                            <Button icon="fas fa-concierge-bell"  class="p-button-rounded p-button-warning mr-2 " @click="openNewreserveation(slotProps.data)" />

                        </template>
                    </Column>
                </DataTable>
                <Sidebar v-model:visible="visibleSidebarInfo"  position="right" class="w-full md:w-20rem lg:w-30rem">
                    <h2>LC Bloc {{ LocalcView.blocname  }} Etage {{ LocalcView.etagename }}</h2>

                    <div class="mb-1 lg:col-4 ">
                        <label for="name" class="mb-1">Nom Residence</label>
                        <InputText  class="input100" :value="LocalcView.residentname" disabled="disabled"/>
                    </div>
                    <div class="mb-1 lg:col-4 ">
                        <label for="name" class="mb-1">Etage Numero</label>
                        <InputText class="input100" :value="LocalcView.etagename" disabled="disabled"/>
                    </div>
                    <hr/>

                    <div class="mb-1 lg:col-4 ">
                        <label for="name" class="mb-1">Prix</label>
                        <InputText autocomplete="off" class="input100" :value="LocalcView.Prix" disabled="disabled"/>
                    </div>
                    <div class="mb-1 lg:col-4 ">
                        <label for="name" class="mb-1">Cotion</label>
                        <InputText autocomplete="off" class="input100" :value="LocalcView.Cotion" disabled="disabled"/>
                    </div>
                    <hr/>
                    <div class="mb-1 lg:col-4 ">
                        <label for="name" class="mb-1">ref Steg</label>
                        <InputText class="input100" :value="LocalcView.isteg" disabled="disabled"/>
                    </div>
                    <div class="mb-1 lg:col-4 ">
                        <label for="name" class="mb-1">ref Sonede</label>
                        <InputText class="input100" :value="LocalcView.isonede" disabled="disabled"/>
                    </div>
                    <div class="mb-1 lg:col-4 ">
                        <label for="name" class="mb-1">ref Gaz</label>
                        <InputText class="input100" :value="LocalcView.igaz" disabled="disabled"/>
                    </div>

                </Sidebar>
                <Dialog  v-model:visible="LocalcaddDialog"  :header=Appartmenttitledialog :modal="true" class="p-fluid fixed-height" :style="{ width: '40vw',height: '50vw' }" :breakpoints="{ '960px': '75vw', '641px': '100vw' }">

                    <div class="field">
 
                            <Steps :model="nestedRouteItems" :readonly="true" />

                    </div>
                    <div v-if="stepone&& !steptwo && !stepthree">
                        <div class="field" v-if="!newLocatairedialog">
                            <label>choisir un locataire existant</label>
                            <AutoComplete placeholder="Search"  :dropdown="true" v-model="selectedLocataireaValue" :suggestions="autoFilteredValue" @complete="searchLocataire($event)" @change="changeselectbox()" field="name" />
                        </div>
                        <div class="field" v-if="!newLocatairedialog">
                            
                            <label >Ou</label>
                            <br/>
                            <Button label="Ajouter Un nouveau locataire"  class="p-button-primary"  @click="newLocataire()"/>
                   
                        </div>
                        <FadeInOut entry="left" exit="right" :duration="100">
                            <div v-if="newLocatairedialog">
                                <div class="field" >
                                    <label>Nom & Prenom</label>
                                    <InputText autocomplete="off"  v-model.trim="Locataireadd.name" />
                                </div>
                                <div class="field">
                                    <label>CIN / Num passport</label>
                                    <InputText autocomplete="off"  v-model.trim="Locataireadd.cin" />
                                </div>
                                <div class="field " >
                                    <label>Num. Tel 1</label>
                                    <InputNumber autocomplete="off"  :useGrouping="false" v-model.trim="Locataireadd.telone" />
                                </div>
                                <div class="field " >
                                    <label>Num. Tel 2</label>
                                    <InputNumber autocomplete="off" :useGrouping="false" v-model.trim="Locataireadd.teltwo" />
                                </div>
                            </div>
                        </FadeInOut>
                    </div>
                    <FadeInOut entry="left" exit="right" :duration="100">
                        <div v-if="!stepone && steptwo && !stepthree">
                            <div class="field " >
                                <label>Prix de location</label>
                                <InputNumber autocomplete="off"   v-model.trim="PaimentLocataire.ploc" />
                            </div>
                            <div class="field " >
                                <label>Cautionnement</label>
                                <InputNumber autocomplete="off"  v-model.trim="PaimentLocataire.pcot" />
                            </div>
                            <div class="field ">
                                <label>Debut De location</label>
                                <Calendar :showIcon="true"  :showButtonBar="true" dateFormat="dd/mm/yy" v-model="PaimentLocataire.startdate"></Calendar>
                            </div>
                            <div class="field ">
                                <label>Fin De location</label>
                                <Calendar :showIcon="true" :showButtonBar="true" dateFormat="dd/mm/yy" v-model="PaimentLocataire.enddate"></Calendar>
                            </div>
                        </div>

                    </FadeInOut>
                    <FadeInOut entry="left" exit="right" :duration="100">
                        <div v-if="!stepone && !steptwo && stepthree">
                            <div class="field " >
                                <label>Index Steg</label>
                                <InputNumber autocomplete="off"  v-model.trim="CompteurLocataire.steg" />
                            </div>
                            <div class="field " >
                                <label>Index Sonede</label>
                                <InputNumber autocomplete="off"  v-model.trim="CompteurLocataire.sonede" />
                            </div>
                            <div class="field ">
                                <label>Index Gaz</label>
                                <InputNumber autocomplete="off"  v-model.trim="CompteurLocataire.gaz" />
                            </div>
                        </div>

                    </FadeInOut>
                    <FadeInOut entry="left" exit="right" :duration="100">
                        <div v-if="!stepone && !steptwo && !stepthree && stepfour">
                            <div class="field " >
                                <form  method="post" enctype="multipart/form-data">

                                    <label>Ajouter des documents</label>
                                    <FileUpload name="demo[]" @uploader="onUpload($event)"  ref="fileInput" :multiple="true" accept="image/*" :maxFileSize="100000000000"  customUpload>
                                        <template #empty>
                                            <p>Drag and drop files to here to upload.</p>
                                        </template>
                                    </FileUpload>
                                </form>
                            
                            </div>
                            <div class="field " >
                                <label>Cette Location avec contrat?</label>
                                <br/>
                                <ToggleButton v-model="WithContracttoggleValue" onLabel="Yes" offLabel="No" :style="{ width: '38em' }" />

                            </div>

                        </div>
                    </FadeInOut>
                    <template #footer>

                        <Button v-if="stepone"   label="Fermer" icon="pi pi-times" class="p-button-text"            @click="hidedialog()"/>
                        <Button v-if="!stepone"  label="Precedant" icon="pi pi-arrow-left" class="p-button-text"    @click="previousstep()"/>                        
                        <Button v-if="stepone"   label="Suivant" icon="pi pi-arrow-right" class="p-button-text"     @click="nextstep()"  :disabled="!Locataireadd.name||!Locataireadd.cin||!Locataireadd.telone||!Locataireadd.teltwo"/>    
                        <Button v-if="steptwo"   label="Suivant" icon="pi pi-arrow-right" class="p-button-text"     @click="nextstep()"  :disabled="!PaimentLocataire.ploc ||!PaimentLocataire.startdate||!PaimentLocataire.enddate"/>                        
                        <Button v-if="stepthree" label="Suivant" icon="pi pi-arrow-right" class="p-button-text"     @click="nextstep()"  :disabled="!CompteurLocataire.steg"/>                        
                        <Button v-if="stepfour"  label="Confirmer" icon="pi pi-check" class="p-button-text"         @click="rentBien()"  :disabled=" !uploadedFiles  "/>

                        
                    </template>
                </Dialog>
                <Dialog  v-model:visible="reservationdialog"  :header=Appartmenttitledialog :modal="true" class="p-fluid fixed-height" :style="{ width: '40vw',height: '50vw' }" :breakpoints="{ '960px': '75vw', '641px': '100vw' }">

                    <div class="field">

                            <Steps :model="nestedRouteItemsreserv" :readonly="true" />

                    </div>
                    <div v-if="stepone&& !steptwo && !stepthree">
                        <div class="field" v-if="!newLocatairedialog">
                            <label>choisir un locataire existant</label>
                            <AutoComplete placeholder="Search"  :dropdown="true" v-model="selectedLocataireaValue" :suggestions="autoFilteredValue" @complete="searchLocataire($event)" @change="changeselectbox()" field="name" />
                        </div>
                        <div class="field" v-if="!newLocatairedialog">
                            
                            <label >Ou</label>
                            <br/>
                            <Button label="Ajouter Un nouveau locataire"  class="p-button-primary"  @click="newLocataire()"/>

                        </div>
                        <FadeInOut entry="left" exit="right" :duration="100">
                            <div v-if="newLocatairedialog">
                                <div class="field" >
                                    <label>Nom & Prenom</label>
                                    <InputText autocomplete="off"  v-model.trim="Locataireadd.name" />
                                </div>
                                <div class="field">
                                    <label>CIN / Num passport</label>
                                    <InputText autocomplete="off"  v-model.trim="Locataireadd.cin" />
                                </div>
                                <div class="field " >
                                    <label>Num. Tel 1</label>
                                    <InputNumber autocomplete="off"  :useGrouping="false" v-model.trim="Locataireadd.telone" />
                                </div>
                                <div class="field " >
                                    <label>Num. Tel 2</label>
                                    <InputNumber autocomplete="off" :useGrouping="false" v-model.trim="Locataireadd.teltwo" />
                                </div>
                            </div>
                        </FadeInOut>
                    </div>
                    <FadeInOut entry="left" exit="right" :duration="100">
                        <div v-if="!stepone && steptwo && !stepthree">
                            <div class="field " >
                                <label>Cautionnement</label>
                                <InputNumber autocomplete="off"  v-model.trim="PaimentLocataire.pcot" readonly="readonly"/>
                            </div>
                            <div class="field ">
                                <label>Date De Debut De location</label>
                                <Calendar :showIcon="true"  :showButtonBar="true" dateFormat="dd/mm/yy" v-model="PaimentLocataire.startdate"></Calendar>
                            </div>
                        </div>

                    </FadeInOut>
                    <FadeInOut entry="left" exit="right" :duration="100">
                        <div v-if="!stepone && !steptwo && stepthree">
                            <h2>Confirmation De reservation</h2>
                            <div class="field">
                                <h5>Locataire</h5>
                                <label>{{ Locataireadd.name }}</label>
                            </div>
                            
                            <div class="field">
                                <h5>Bien</h5>
                                <label>Residence  {{ locationdata.residentname }} Etage {{locationdata.etagename==0 ? "RDC" :locationdata.etagename}}  Appartement  {{ locationdata.appartementname  }}</label>
                            </div>
                            <div class="field">
                                <h5>Cautionnement</h5>
                                <label>{{ PaimentLocataire.pcot }}</label>
                            </div>
                            <div class="field">
                                <h5>Date de Debut de location</h5>
                                <label>{{ PaimentLocataire.startdate.toLocaleDateString() }}</label>
                            </div>
                        </div>

                    </FadeInOut>

                    <template #footer>

                        <Button v-if="stepone"   label="Fermer" icon="pi pi-times" class="p-button-text"            @click="hidedialog()"/>
                        <Button v-if="!stepone"  label="Precedant" icon="pi pi-arrow-left" class="p-button-text"    @click="previousstep()"/>                        
                        <Button v-if="stepone"   label="Suivant" icon="pi pi-arrow-right" class="p-button-text"     @click="nextstep()"   :disabled="!Locataireadd.name||!Locataireadd.cin||!Locataireadd.telone||!Locataireadd.teltwo"/>    
                        <Button v-if="steptwo"   label="Suivant" icon="pi pi-arrow-right" class="p-button-text"     @click="nextstep()"   :disabled="!PaimentLocataire.pcot ||!PaimentLocataire.startdate" />                        
                        <Button v-if="stepthree" label="Confirmer" icon="pi pi-check" class="p-button-text"         @click="submitreserv()" />                        

                        
                    </template>
                </Dialog>
                
            </div>
        </div>
    </div>
</template>
