import axios from 'axios';
let tokenjwt = localStorage.getItem('token');

export default class LocalCom {

    getLocalC() {
     
    let respo;
    try {
      const response =  axios.get(`${process.env.VUE_APP_HOST_BACKEND}/location/get/view/familliale/!Isrent`, {
        headers: {
          Authorization: `${tokenjwt}`,
        },
      });
      respo=response;
    } catch (error) {
      console.error(error);
    }
    
    return respo;

  }
  rentLocC(params,body) {  
    let resp = {
      type: null,
      msg: null,
      summary: null,
      data: null,
    }
    const request =  axios.post(`${process.env.VUE_APP_HOST_BACKEND}/location/post/location/AppF/${params.idres}/${params.idbloc}/${params.idetage}/${params.appartementid}/`,{body} ,{
      headers: {
        Authorization: `${tokenjwt}`,
      },
    })
    .then(response => {
      if (response.data.success){
        resp.type = "success";
        resp.msg = "Appartement loué avec succès";
        resp.summary = "Action Terminée";
        resp.data=response.data.result
      }
      else{
        resp.type = "error";
        resp.msg = response.data.msg;
        resp.summary = "ERREUR";
      }

      return resp;
      
    })
    .catch(error => {
      alert('Wrong user account ' +error )
      return resp;
    });

    return request;

  }
  reservLoc(params,body) {  
    let resp = {
      type: null,
      msg: null,
      summary: null,
      data: null,
    }
    const request =  axios.post(`${process.env.VUE_APP_HOST_BACKEND}/reservation/post/AppF/${params.idres}/${params.idbloc}/${params.idetage}/${params.appartementid}/`,{body} ,{
      headers: {
        Authorization: `${tokenjwt}`,
      },
    })
    .then(response => {
      if (response.data.success){
        resp.type = "success";
        resp.msg = "Appartement reservé avec succès";
        resp.summary = "Action Terminée";
        resp.data=response.data.result
      }
      else{
        resp.type = "error";
        resp.msg = response.data.msg;
        resp.summary = "ERREUR";
      }

      return resp;
      
    })
    .catch(error => {
      alert('Wrong user account ' +error )
      return resp;
    });

    return request;

  }

}
